import React,{ useState, useEffect, useRef, useCallback } from 'react';
import io from 'socket.io-client';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import CardGrid from '@vkontakte/vkui/dist/components/CardGrid/CardGrid'
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Spinner from '@vkontakte/vkui/dist/components/Spinner/Spinner'
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Search from '@vkontakte/vkui/dist/components/Search/Search'
import List from '@vkontakte/vkui/dist/components/List/List'
import CardScrol from "@vkontakte/vkui/dist/components/CardScroll/CardScroll"
import Card from '@vkontakte/vkui/dist/components/Card/Card'
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Footer from '@vkontakte/vkui/dist/components/Footer/Footer'
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon24MoreHorizontal from '@vkontakte/icons/dist/24/more_horizontal'

import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import { ModalRoot, ModalPage } from '@vkontakte/vkui';
import { FormLayout } from '@vkontakte/vkui';
import useTaskHook from '../hooks/useTaskHook';
import CardInfo from "./Card_Info"
import '../css/ThematicsTask.css'



function isEmpty(object) {
    return !(JSON.stringify(object) == "{}");
  }
function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

var chooseCard = {}
const ThematicsTask = (props) => {

    const size_card = ["l","m"]
    const [info,setInfo]=useState({});
    const [pageNumber, setPageNumber] = useState(0);
    const [activeModal, setActiveModal] = useState(null);

    const {loading, error, tasks, hasMore} = useTaskHook(pageNumber,props.dataTransfer.name);

    const observer = useRef();
    const lastCardElementRef = useCallback(node =>{
        // console.log(node);
        if (loading) return
        if (observer.current) observer.current.disconnect();
        observer.current = new IntersectionObserver(entries =>{
            if (entries[0].isIntersecting){
                setPageNumber(prevPageNumber => prevPageNumber + 1);
            }
        })
        if (node) observer.current.observe(node);
    },[loading,hasMore])
    
    return(
        <Panel id={props.id}>
            <PanelHeader
                left={<PanelHeaderButton onClick={() => {props.setActivePanelAllTask("AllTask")}}><Icon24Back/></PanelHeaderButton>}
                right={<PanelHeaderButton><Icon24MoreHorizontal /></PanelHeaderButton>} 
        >{props.dataTransfer.name}</PanelHeader> 
        <CardInfo activeModal={activeModal} setActiveModal={setActiveModal} 
                          chooseCard={chooseCard} setActiveStory={props.setActiveStory} id_v={props.id_v}></CardInfo>
        <div className="CardGridThematics" id="grid" >
            {tasks.map((task,index) => {
                if (tasks.length === index+1){
                    return (
                        <div key={index}>
                        
                        <Card  size="l" mode="shadow" className="CardBodyThematics" onClick={()=>{
                                
                                chooseCard = task;
                                setActiveModal("modal_inform");
                            }}>
                            <Cell>
                                <div className='NameTask'>{task.name}</div>
                                <div className='PriceTask'>Цена: {task.price}</div>
                                <div className='DeadlineTask'>Deadline:{task.deadline}</div>
                            </Cell>
                        </Card>
                        <div ref={lastCardElementRef} style={{background: 'black'}}></div>
                        </div>);
                    
                }else{
                    return (
                        <Card size="l" key={index} mode="shadow" className="CardBodyThematics" onClick={()=>{
                            chooseCard= task;
                            setActiveModal("modal_inform");
                        }}>
                            <Cell>
                                <div className='NameTask'>{task.name}</div>
                                <div className='PriceTask'>Цена: {task.price}</div>
                                <div className='DeadlineTask'>Deadline:{task.deadline}</div>
                            </Cell>
                        </Card>);
                }
                
            })}
            <div>{loading?(<Spinner size="regular" style={{ paddingTop: 20,paddingBottom: 20 }} />):
                           <Footer className="FooterThematics">{tasks.length} заданий</Footer>}</div>
            <div>{error && "Error"}</div>
            
        </div>
        </Panel>
        
    );
	
};

ThematicsTask.propTypes = {
};

export default ThematicsTask;
