import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Textarea from '@vkontakte/vkui/dist/components/Textarea/Textarea'
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import List from "@vkontakte/vkui/dist/components/List/List"
import Checkbox from '@vkontakte/vkui/dist/components/Checkbox/Checkbox'
import Input from '@vkontakte/vkui/dist/components/Input/Input'
import Select from '@vkontakte/vkui/dist/components/Select/Select'
import Search from "@vkontakte/vkui/dist/components/Search/Search"

import '../css/GiveTask.css'

import { FormLayout } from '@vkontakte/vkui';
import { Alert } from '@vkontakte/vkui';




const GiveTask = (props) => {        

    const thematics = [
        {id: 1, name: "Программирование"},
        {id: 2, name: "Доставка"},
        {id: 3, name: "Учёба"},
        {id: 4, name: "Дизайн"},
        {id: 5, name: "Пение"},
        {id: 6, name: "Готовка"},
        {id: 7, name: "Minecraft"},
        {id: 8, name: "Общение"},
        {id: 9, name: "Рисование"},
        {id: 10, name: "Скульптура"},
        {id: 11, name: "Прочее"},        
      ];

    const [search, SetSearch] = useState("");

    const onChange = (e) =>
    {
        SetSearch(e.target.value);        
    }   

    const onClick = (e) =>
    {        
        createTask();    
      //   props.setPopout(
      //       <Alert
      //   actions={[{
      //     title: 'Отмена',
      //     autoclose: true,
      //     mode: 'cancel'
      //   }, {
      //     title: 'Добавить',
      //     autoclose: true,
      //   }]}
      // >          
      //   <h2>Подтвердите действие</h2>
      //   <p>Добавить пользователю право на модерацию контента.</p>
      // </Alert>
      //   ); 
    }

    const createTask = ()=>
    {   
        props.setPopout(<ScreenSpinner size='large' />);
        const name = document.getElementById("TaskName").value;
        const about = document.getElementById("About").value;
        const deadLine = document.getElementById("Dead-line").value;
        const thematic = document.getElementById("Thematic").value;
        const price = document.getElementById("TaskPrice").value;
        console.log(thematic, thematics.find(({id}) =>{return id == thematic}).name);
        fetch(`https://task-today-tpuhack.herokuapp.com/create_task?name=${name}&about=${about}&categori=${thematics.find(({id}) =>{return id == thematic}).name}&price=${price}&id_employer=id${props.id_v}&deadline=${deadLine}`)
    		.then(response => response.json())
    		.then(result =>{
        	try{
                props.setPopout(null);
                // console.log(result)
				if (result == "0") {
          // console.log(thematics.find(({id}) =>{return id == thematic}).name);
					alert("Ошибка подачи задания");
				}else{
                    alert("Задание подано")
                    fetch(`https://task-today-tpuhack.herokuapp.com/set_new_task?task_id=${result}&id=id${props.id_v}`)
    		        .then(response => response.json())
    		        .then(result2 =>{   
                        props.setActiveStory("Task")                         
                        console.log(result2);
                    });				
				}				
            }
            catch(err){props.setPopout(null); console.log(err);
            }});
    }    
    return(
        <Panel id={props.id}>
        
		<PanelHeader className='PanelHeader'>Дать задание</PanelHeader>
        <FormLayout>            
            <div className="MainText" >
		        <div >Укажите ваше задание</div>
            </div>            
            <Input id="TaskName" top = "Название" type="text" defaultValue="" status="default"/>
            <Textarea className="About" id="About" top="О задании" placeholder="Расскажите что должен делать c:" status="default"></Textarea>
            <Input id="Dead-line" top = "Дедлайн" type="date" defaultValue="" status="default"/>                       
            <Select id="Thematic" placeholder="Категория"status="default">
                {thematics.map(thematic => <option key={thematic.id} value={thematic.id}>{thematic.name}</option>)}                
            </Select>                              
            <Input id="TaskPrice" top = "Вознаграждение" type="text" defaultValue="" status="default"/>
            <Button size="xl" onClick={createTask} status="default">Добавить</Button>  
        </FormLayout>
        
        
	</Panel>
    );
	
};

GiveTask.propTypes = {
	
};

export default GiveTask;
