import React, { useState, useEffect } from 'react';
import Epic from '@vkontakte/vkui/dist/components/Epic/Epic'
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Header from '@vkontakte/vkui/dist/components/Header/Header'
import Card from '@vkontakte/vkui/dist/components/Card/Card'
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon24MoreHorizontal from '@vkontakte/icons/dist/24/more_horizontal'
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert'
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar'
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline'
import Icon28UserOutline from '@vkontakte/icons/dist/28/user_outline'
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications'
import Icon28More from '@vkontakte/icons/dist/28/more'
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem'
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import { FormLayout, Input } from '@vkontakte/vkui';
import '../css/Candidates.css'

const AgreeWithTask = (props,person,setEmployeeInfo) =>{
  
  fetch("https://task-today-tpuhack.herokuapp.com/agree_employer?task_id="+props.dataTransfer._id+"&category="+props.dataTransfer.categori+"&id="+person.vk_id).then(response => response.json())
  .then(result =>{
    switch (result) {
      case 1 : 
        alert("Работник нанят")
        props.setActivePanelTask("Task")
      break;
      case 0 : 
        alert("Работник не нанятб попробуйте ещ раз")
        props.setActivePanelTask("Task")
      break;
      default:
        alert("Error  Заявка неподана")
        props.setActivePanelTask("Task")
      break;
    }	
  })
}

const Candidates = (props) => {

 
  const [EmployeeInfo,setEmployeeInfo]=useState(null);

  const [activeModal, setActiveModal] = useState(null);

  useEffect(() => {
      props.setPopout(<ScreenSpinner size='large' />);
      async function GetData(){
        fetch("https://cors-anywhere.herokuapp.com/https://task-today-tpuhack.herokuapp.com/get_candidates?task_id="+props.dataTransfer._id+"&category="+props.dataTransfer.categori).then(response => response.json())
        .then(result =>{
          console.log(result);
          setEmployeeInfo(result);
          props.setPopout(null);
          
        })
      }
      GetData();
  }, []);



	return(
		<Panel id={props.id}>
            
       <PanelHeader
        left={<PanelHeaderButton onClick={() => {props.setActivePanelTask("Task")}}><Icon24Back/></PanelHeaderButton>}
        right={<PanelHeaderButton><Icon24MoreHorizontal /></PanelHeaderButton>} 
        
        >Заявки</PanelHeader>
        <FormLayout className="FormBody" >
        {(EmployeeInfo&&EmployeeInfo.length != 0)?(EmployeeInfo.map((person,index) => 
            <Card size="l" mode="shadow" key={index} >
                <Cell className="CellBody" key={index}>
                    <div  className="DataBody">
                        <div>{person.vk_id}</div>
                        <div>О работнике : {person.about}</div>
                        <div>Рейтинг {person.rate}</div>
                    </div>
                    <Button  className="BT" onClick={()=>{props.setPopout(<Alert actionsLayout="vertical"
                                                          actions={[{
                                                                      title: 'Да, согласен',
                                                                      autoclose: true,
                                                                      mode: 'destructive',
                                                                      action: () => AgreeWithTask(props,person,setEmployeeInfo),
                                                                    }, {
                                                                      title: 'Отмена',
                                                                      autoclose: true,
                                                                      mode: 'cancel'
                                                                    }]}
                                                                    onClose={() => props.setPopout(null)}>
                                                          <h2>Подтвердите действие</h2>
                                                          <p>Вы уверены, что хотите выбрать этого пользователя на работу?</p>
                                                          </Alert>)
                      }}>Нанять</Button>
                </Cell>
            </Card>
        )):((EmployeeInfo == "0")?(<Div>
          Пока тут пусто
        </Div>):(<Div>Заявок пока нету</Div>))
            

        }
        </FormLayout>
          
    </Panel>
		
	
	);
};

Candidates.propTypes = {
};

export default Candidates;
