import {useEffect, useState} from 'react';

export default function useTaskHook(pageNumber,category) {
    
    var skip = 0;
    const limit = 5;

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [tasks, setTasks] = useState([]);
    const [hasMore, setHasMore] = useState(true);

    skip += pageNumber*limit;

    useEffect(()=>{
        if(hasMore){
        setLoading(true);
        setError(false);
        fetch(`https://task-today-tpuhack.herokuapp.com/get_task_v2?skip=${skip}&limit=${limit}&category=${category}`)
        .then(responce => responce.json())
        .then(results => {
            setTasks(prevTaks =>{
                // console.log([...prevTaks, results ]);
                return [...prevTaks, ...results ]
                // return results;
                
            })
            setHasMore(results.length > 0);
            setLoading(false);
            
        }).catch(err =>{
            setError(true);
            return;
        })
    }else{
        setLoading(false);
        setError(false);
    }

    },[pageNumber]);
    return {loading,error,tasks,hasMore};
}
