import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Textarea from '@vkontakte/vkui/dist/components/Textarea/Textarea'
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import List from "@vkontakte/vkui/dist/components/List/List"
import Checkbox from '@vkontakte/vkui/dist/components/Checkbox/Checkbox'
import Search from "@vkontakte/vkui/dist/components/Search/Search"


import '../css/FirstConnect.css'
import { FormLayout } from '@vkontakte/vkui';

const createPerson = (props) =>{
    fetch("https://task-today-tpuhack.herokuapp.com/create_employee?id=id"+props.id_v+"&phone=892346750213&"+
    "about="+document.getElementById("About").value)
    		.then(response => response.json())
    		.then(result =>{
        	try{
				if (result == "1") {
					props.setActiveStory('Task')
				}else{
                    props.setActiveStory('FirstConnect')
                }
                window.location.reload();
                props.setActiveStory('Task')
                console.log("GO HOME"); 
        }catch(err){ console.log(err);
        }

    	}).catch(err => console.log(err))

}

const FirstConnect = (props) => {        

    const thematics = [
        {id: 1, name: "Программирование"},
        {id: 2, name: "Доставка"},
        {id: 3, name: "Учёба"},
        {id: 4, name: "Рисование"},
        {id: 5, name: "Дизайн"},
        {id: 6, name: "Пение"},
        {id: 7, name: "Готовка"},
        {id: 8, name: "Minecraft"},
        {id: 9, name: "Общение"},
        {id: 10, name: "Прочее"},
        
      ];
    props.setPopout(null);
    const [search, SetSearch] = useState("");

    const onChange = (e) =>
    {
        SetSearch(e.target.value);        
    }   

    return(
        <Panel id={props.id}>
        
		<PanelHeader className='PanelHeader'>TaskToday</PanelHeader>
        <FormLayout>
            <div className="MainText">
                <div>Добро пожаловать</div>
		        <div>Для удачного использования введите некоторую информацию и разрешите уведомления</div>
            </div>
            <Textarea className="About" id="About" top="О себе" placeholder="Расскажите что-нибудь c:"></Textarea>
            <Button size="xl" onClick={()=>{createPerson(props)}}>Закончить</Button>            
            <Search top="Чем вы будете заниматься?" value={search} onChange={onChange} after={null}/>
                {thematics.length > 0 &&
                    <List>
                        {thematics.filter(({name})=>{return name.toLowerCase().indexOf(search.toLowerCase())>-1}).map(thematic => <Checkbox key={thematic.id}>{thematic.name}</Checkbox>)}
                    </List>
                }            
            
        </FormLayout>
        
        
	</Panel>
    );
	
};

FirstConnect.propTypes = {
	
};

export default FirstConnect;
